.control-button {
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  padding: 0.5rem;
  margin: 1rem 0;
  border-radius: 12px;
  background-color: #4b4b4b;
  width: 80px;
  height: 80px;
}

.control-button span{
  font-size: 2.0em;
}

.control-button button{
  height: 100%;
}